import React from "react";
import ReactCompareImage from "react-compare-image";
import design1 from "../Images/design1.png";
import design2 from "../Images/design2.png";
import design3 from "../Images/design3.png";
import design4 from "../Images/design4.png";

const Design = () => {
  return (
    <div>
      <h2 className="section--header">Design</h2>
      <div style={{ display: "flex" }}>
        <div className="section--compare-container">
          <ReactCompareImage
            leftImage={design1}
            rightImage={design2}
            sliderPositionPercentage="0.3"
          />
        </div>
        <div className="section--compare-container">
          <ReactCompareImage
            leftImage={design3}
            rightImage={design4}
            sliderPositionPercentage="0.05"
          />
        </div>
      </div>
      <ul className="section--list">
        <li>
          Having the navigation stick to the top is good for users as it will
          allow them to access other pages quickly, which may be more common if
          they are researching exhibits and other articles on the website.
          Perhaps on the discover tab having a secondary navigation takes up too
          much space on the screen and could be made collapsible, as well as
          making the navigation translucent after scrolling from the top.
        </li>
        <li>
          Switching the navigation of the website to a dropdown / collapsible
          version on smaller screen sizes really improves usability on mobile
          and other touch devices with less accurate inputs.
        </li>
        <li>
          The footer is rather large, which may not be too much of a problem on
          desktops, but may be harder to navigate through on smaller devices.
        </li>
        <li>
          Each section of the site having its own designated colour is
          aesthetically pleasing, and helps users identify which part of the
          website they are on easily.
        </li>
        <li>
          Editing styling on the cards used to preview articles and sections so
          that they have uniform heights on the containers and images would
          perhaps make the layout look cleaner, but may be difficult depending
          on headline and caption length.
        </li>
        <li>
          There seems to be good cross-browser support, and the website appears
          functionally and visually indistinguishable on Firefox, Edge and
          Chrome.
        </li>

        <li>
          The hover/click animation used on the drop-down sections seems
          counter-intuitive, as the button greys out after the section is
          hovered or clicked on.
        </li>
        <li>
          Adding font weight onto the sub-headings of transport on the Visit
          page may make it easier for users to quickly scan the page to find the
          information they are looking for.
        </li>
        <li>
          Adding the transition CSS property onto many of the hover effects
          could give a smoother and more satisfying user experience.
        </li>
      </ul>
    </div>
  );
};

export default Design;
