import React from "react";
import security from "../Images/security.png";

const Security = () => {
  return (
    <div>
      <h2 className="section--header">Security</h2>
      <img src={security} alt="image of url" />
      <ul className="section--list">
        <li>
          The website is currently not using HTTPS encryption or HTTPS2, which
          would improve the security of the site and speed up the fetching of
          the data.
        </li>
        <li>
          Links to external sites (the shop and images) utilises target=_blank
          but doesn’t have rel=”noopener” attribute, which may cause performance
          issues and could lead to security risks.
        </li>
        <li>
          The website is currently utilising an older version of jQuery, which
          may make it vulnerable to XSS or DDOS attacks.
        </li>
      </ul>
    </div>
  );
};

export default Security;
