import React from "react";
import performance1 from "../Images/performance1.png";
import performance2 from "../Images/performance2.png";
import performance3 from "../Images/performance3.png";

const Performance = () => {
  return (
    <div>
      <h2 className="section--header">Performance</h2>
      <div className="section--images">
        <img src={performance1} alt="audit of website speed" />
        <img src={performance2} alt="audit breakdown" />
        <img src={performance3} alt="audit file breakdown" />
      </div>
      <ul className="section--list">
        <li>
          The page size is relatively large, which could be an issue for users
          with a weak internet connection or those using mobile phones.
        </li>
        <li>
          Many of the files and images could be lazy loaded so that they are
          only requested when needed which could improve the performance of the
          site for users with imperfect connections and help those utilising
          mobile data.
        </li>
        <li>
          Many CSS and JS files, including external files like jQuery, are being
          initially loaded, which is slowing down the render of the page which
          could be cut down on.
        </li>
        <li>
          The search function through the navigation providing popular search
          terms is helpful, but it scrolls the page to the top and locks the
          user from scrolling, which could hinder the user if they wanted to
          search something related to what they were currently viewing on the
          page.
        </li>
        <li>
          The website has decent accessibility, but could be improved by
          changing the colour scheme to have a higher contrast, and adding alt
          tags to images to assist screen readers.
        </li>
        <li>
          The search function on the What’s On page is fast and doesn’t require
          a page refresh, as well as allowing filtering after the search has
          been completed which creates a good user experience.
        </li>
      </ul>
    </div>
  );
};

export default Performance;
